import axios from "axios";
import router from "../../router";
const API_URL = "/users/";

const state = {
  loggedUser: {},
  token: localStorage.getItem("token") || "",
  status: "",
  error: null,
};

const getters = {
  isLoggedIn: (state) => !!state.token,
  loggedUser: (state) => state.loggedUser,
  error: (state) => state.error,
};

const actions = {
  // Login action
  async login({ commit }, user) {
    commit("loginRequest");
    console.log(user);
    try {
      const res = await axios.post(`${API_URL}login`, user);
      if (res.data.success) {
        console.log(res);
        const token = res.data.token;
        const loggedUser = res.data.user;
        localStorage.setItem("token", token);
        localStorage.setItem("userId", loggedUser._id);
        axios.defaults.headers.common.Authorization = token;
        commit("loginSuccess", { token, loggedUser });
      }

      return res;
    } catch (error) {
      commit("loginError", error);
    }
  },

  // Register action
  async register({ commit }, user) {
    commit("registerRequest");
    try {
      const res = await axios.post(`${API_URL}register`, user);
      if (res.data.success) {
        commit("registerSuccess");
      }
      return res;
    } catch (error) {
      commit("registerError", error.response.data.message);
    }
  },

  // Logout actions
  async logout({ commit }) {
    await localStorage.removeItem("token");
    await localStorage.removeItem("userId");
    commit("logout");
    delete axios.defaults.headers.common.Authorization;
    router.push("/");
    return;
  },

  // Get user profile
  async getUserProfile({ commit }) {
    commit("getUserProfileRequest");
    try {
      const res = await axios.get(`${API_URL}profile`);
      if (res.data.success) {
        commit("getUserProfileSuccess", res.data.user);
      }
      return res;
    } catch (err) {
      console.log(err);
    }
  },
};

const mutations = {
  loginRequest(state) {
    state.error = null;
    state.status = "loading";
  },
  loginSuccess(state, { token, loggedUser }) {
    state.status = "success";
    state.token = token;
    state.loggedUser = loggedUser;
    state.error = null;
  },
  loginError(state, error) {
    state.error = error.response.data.message;
  },
  registerRequest(state) {
    state.error = null;
    state.status = "loading";
  },
  registerSuccess(state) {
    state.error = null;
    state.status = "success";
  },
  registerError(state, error) {
    state.error = error;
  },
  logout(state) {
    state.error = null;
    state.status = "";
    state.loggedUser = "";
    state.token = "";
  },
  getUserProfileRequest(state) {
    state.error = null;
    state.status = "loading";
  },
  getUserProfileSuccess(state, user) {
    state.status = "success";
    state.loggedUser = user;
  },
};

export default {
  state,
  actions,
  getters,
  mutations,
};
