import axios from "axios";
const API_URL = "/users/";
const state = {
  status: "",
  people: null,
  selectedUser: null,
};

const getters = {
  people: (state) => state.people,
  selectedUser: (state) => state.selectedUser,
};

const actions = {
  // SOCKET_online({ commit }) {
  //   this.getPeople({ commit });
  // },
  // Get people action
  async getPeople({ commit }) {
    commit("getPeopleRequest");
    try {
      const res = await axios.get(`${API_URL}`);
      if (res.data.success) {
        commit("getPeopleSuccess", res.data.value);
      }
      return res;
    } catch (error) {
      console.log(error);
    }
  },

  // Follow person
  async followPerson({ commit }, personId) {
    commit("followPersonRequest");
    try {
      const res = await axios.post(`${API_URL}follows`, { personId });
      if (res.data.success) {
        commit("followPersonSuccess");
      }
      return res;
    } catch (error) {
      console.log(error);
    }
  },

  // Post profile photo
  async postProfilePhoto({ commit }, photo) {
    commit("postProfilePhotoRequest");
    try {
      const res = await axios.post(`${API_URL}profile`, photo);
      if (res.data.success) {
        commit("postProfilePhotoSuccess");
      }
      return res;
    } catch (error) {
      console.log(error);
    }
  },

  // Get single person data
  async getProfile({ commit }, username) {
    commit("getProfileRequest");
    try {
      const res = await axios.get(`${API_URL}${username}`);
      if (res.data.success) {
        commit("getProfileSuccess", res.data.user);
      }

      return res;
    } catch (err) {
      console.log(err);
    }
  },
};

const mutations = {
  getPeopleRequest(state) {
    state.status = "loading";
  },
  getPeopleSuccess(state, people) {
    state.status = "success";
    state.people = people;
  },
  followPersonRequest(state) {
    state.status = "loading";
  },
  followPersonSuccess(state) {
    state.status = "success";
  },
  postProfilePhotoRequest(state) {
    state.status = "loading";
  },
  postProfilePhotoSuccess(state) {
    state.status = "success";
  },
  getProfileRequest(state) {
    state.error = null;
    state.status = "loading";
  },
  getProfileSuccess(state, user) {
    state.status = "success";
    state.selectedUser = user;
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
};
